.update_post {
  margin: 3rem;
}

.update_post .file_post_body {
  padding: 10px;
  text-align: left;
}

.update_post .file_post_body .ql-editor {
  min-height: 100px;
}

.update_post .post_section_title {
  font-size: 15px;
  font-weight: 600;
  border-radius: 5px;
  background-color: var(--gen-bg-color);
  width: max-content;
  padding: 2px 8px;
  margin: 10px 0;
}

.update_post .post_header {
  width: 100%;
  border-radius: 8px;
  background: var(--gen-bg-color);
  padding: 4px 5px;
}

.update_post .post_header .post_date p {
  font-size: 14px;
  color: var(--text-color);
}

.post_header .post_share_with {
  gap: 10px;
}

.post_header .post_share_with img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.5);
  border: 1px solid var(--gen-color);
}

.post_header .post_share_with p {
  font-size: 14px;
  color: var(--text-color);
}

.img_post_body {
  margin: 10px 20px;
}

.img_post_body p {
  font-size: 15px;
  /* line-height: 1.5em; */
  color: var(--text-color);
}

/* file post */
.post_images {
  flex-flow: wrap;
  gap: 10px;
}

.post_file_img .file_handler #input[type="file"] {
  display: none;
}

.update_post .post_image {
  position: relative;
}

.update_post .post_images img {
  object-fit: cover;
  width: 100%;
  border-radius: 10px;
}

.update_post .image_container:hover .btn_container_for_modal {
  opacity: 1;
  visibility: visible;
}

.update_post .btn_container_for_modal {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 8px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in;
  background-color: #00000077;
  border-radius: 10px;
  gap: 8px;
}

.update_post .btn_container_for_modal i {
  color: #fff;
  padding-right: 4px;
}

.update_post .image_container {
  position: relative;
  width: 45%;
  overflow: hidden;
  border-radius: 10px;
}

.post_file {
  border-radius: 5px;
  background: linear-gradient(to right, var(--gen-color) #39c09e, #2bc4cfcc);
  padding: 6px;
  width: max-content;
  margin: 5px;
  max-width: 100%;
}

.files_icon_container p {
  max-width: 100%;
  word-break: break-word;
  font-size: 14px;
}

.update_post .image-upload {
  font-size: 15px;
}
.update_post .image-upload p {
  font-size: 14px;
}

/* download button */
.update_post .btn_download {
  position: relative;
  width: 60px;
  height: 26px;
  border: 1px solid var(--gen-bg-color);
  background-color: var(--gen-color);
  overflow: hidden;
  border-radius: 6px;
  margin-right: 5px;
}

.btn_download,
.button_download_icon,
.button_download_text {
  transition: all 0.3s;
}

.update_post .btn_download .button_download_text {
  color: #fff;
}

.update_post .btn_download .button_download_icon {
  position: absolute;
  transform: translateX(109px);
  height: 100%;
  width: 39px;
  background-color: var(--gen-bg-color);
  color: #fff;
}

.btn_download .button_download_icon > i {
  color: #fff;
  font-size: 18px;
}

.update_post .btn_download:hover {
  background: var(--gen-color);
}

.btn_download:hover .button_download_text {
  color: transparent;
}

.btn_download:hover .button_download_icon {
  width: 148px;
  transform: translateX(0);
}

.btn_download:active .button_download_icon {
  background-color: var(--gen-color);
}

.btn_download:active {
  border: 1px solid var(--gen-color);
}
/* End of download button */

.share_post_on {
  margin-top: 1.5rem;
}

.share_post_on h3 {
  font-size: 19px;
  color: var(--text-color);
}

.post_boxes {
  margin-top: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(180px, auto));
  grid-gap: 10px;
}

.post_boxes .post_box {
  width: 100%;
}

.post_boxes .post_box select {
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
  font-size: 14px;
  color: var(--text-color);
  border: 1px solid transparent;
  border-left-color: var(--gen-color);
  border-left-width: 3px;
  border-radius: 8px;
  outline: none;
  transition: border 0.3s ease;
  background-color: var(--input-box-color);
}

.updatePost_edit {
  margin-top: 1.8rem;
}

/* Responsive */
@media (max-width: 768px) {
  .update_post {
    margin: 1rem;
  }
  .post_header .post_share_with img {
    width: 45px;
    height: 45px;
  }
  .update_post .btn_container_for_modal {
    gap: 2px;
  }
  .update_post .btn_container_for_modal i {
    font-size: 13px;
  }
  .update_post .btn_container_for_modal button {
    padding: 3px 8px;
  }
}

@media (max-width: 480px) {
  .update_post {
    margin: 0;
  }
  .update_post .file_post_body .ql-toolbar button {
    height: 20px;
    padding: 3px;
    width: 20px;
  }
  .post_images {
    flex-direction: column;
    width: 100%;
  }
  .update_post .image_container {
    width: 100%;
  }
}

@media (max-width: 375px) {
  .post_boxes {
    grid-template-columns: repeat(auto-fit, minmax(150px, auto));
  }
}
